import React, { useState, useEffect } from "react";
import './Footer.css';

import { motion } from "framer-motion";
import { useInView } from "react-intersection-observer";


function Footer() {

    const [showTopBtn, setShowTopBtn] = useState(false);
    const [opacity, setOpacity] = useState(0);

    useEffect(() => {
        const handleScroll = () => {
            if (window.scrollY > 300) {
                setShowTopBtn(true);
                setOpacity(1);
            } else {
                setOpacity(0);
                setTimeout(() => setShowTopBtn(false), 300);
            }
        };

        window.addEventListener("scroll", handleScroll);
        return () => window.removeEventListener("scroll", handleScroll);
    }, []);

    const scrollToSection = (id) => {
        const section = document.getElementById(id);
        if (section) {
            section.scrollIntoView({ behavior: "smooth" });
        }
    };

    const scrollToTop = () => {
        window.scrollTo({ top: 0, behavior: "smooth" });
    };

    // ANIMATION
    const { ref: ref7, inView: inView7 } = useInView({ triggerOnce: true, threshold: 0.3 });

    return (
        <footer className='mb-hidden FooterPage'>
            <div className='container mx-auto'>
                <div className='flex items-center justify-center'>
                    <motion.div
                        ref={ref7}
                        initial={{ opacity: 0, y: -100 }}
                        animate={{ opacity: inView7 ? 1 : 0, y: inView7 ? 0 : -100 }}
                        transition={{ duration: 1, type: "spring", stiffness: 50 }}
                    >
                        <p className='text-[#000] text-sm font-semibold'>© 2025 Sumit Singh Rawat. All Rights Reserved.</p>
                    </motion.div>
                </div>
                <i className="fa fa-angle-up animate-bounce scrollTop" style={{ opacity: opacity, }} onClick={scrollToTop}></i>
            </div>
        </footer>
    )
}

export default Footer