import React from "react";
import { Swiper, SwiperSlide } from "swiper/react";
import "swiper/css";
import "swiper/css/pagination";
import "swiper/css/navigation";
import { Navigation, Pagination, Autoplay } from "swiper/modules";

import WSPImg from '../../assets/images/Projects/UISwiper/WSP.jpg'
import WMAXImg from '../../assets/images/Projects/UISwiper/WMAX.jpg'
import WMWImg from '../../assets/images/Projects/UISwiper/WMW.jpg'
import WSBImg from '../../assets/images/Projects/UISwiper/WSB.jpg'
import WTSSImg from '../../assets/images/Projects/UISwiper/WTSS.jpg'


const UIWebSwiper = () => {
    return (
        <Swiper
            modules={[Navigation, Pagination, Autoplay]}
            spaceBetween={0}
            slidesPerView={3}
            navigation
            pagination={{ clickable: true }}
            autoplay={{ delay: 3000, disableOnInteraction: false }}
        >
            <SwiperSlide className="py-4 px-2">
                <div className="CustomSwiperBox">
                    <img className="w-full mb-4" src={WSPImg} alt="" />
                    <h3>Sarkari Pariksha</h3>
                    <p>I Designed this UI using:</p>
                    <span>Figma</span>
                </div>
            </SwiperSlide>
            <SwiperSlide className="py-4 px-2">
                <div className="CustomSwiperBox">
                    <img className="w-full mb-4" src={WMAXImg} alt="" />
                    <h3>Maxworth Systems</h3>
                    <p>I Designed this UI using:</p>
                    <span>Figma</span>
                </div>
            </SwiperSlide>
            <SwiperSlide className="py-4 px-2">
                <div className="CustomSwiperBox">
                    <img className="w-full mb-4" src={WMWImg} alt="" />
                    <h3>Meri Website</h3>
                    <p>I Designed this UI using:</p>
                    <span>Figma</span>
                </div>
            </SwiperSlide>
            <SwiperSlide className="py-4 px-2">
                <div className="CustomSwiperBox">
                    <img className="w-full mb-4" src={WSBImg} alt="" />
                    <h3>Santa Brigade</h3>
                    <p>I Designed this UI using:</p>
                    <span>Figma</span>
                </div>
            </SwiperSlide>
            <SwiperSlide className="py-4 px-2">
                <div className="CustomSwiperBox">
                    <img className="w-full mb-4" src={WTSSImg} alt="" />
                    <h3>The Second School</h3>
                    <p>I Designed this UI using:</p>
                    <span>Figma</span>
                </div>
            </SwiperSlide>
        </Swiper>
    );
};

export default UIWebSwiper;