import React from "react";
import { Swiper, SwiperSlide } from "swiper/react";
import "swiper/css"; // Import Swiper styles
import "swiper/css/pagination"; // Optional styles
import "swiper/css/navigation"; // Optional styles
import { Navigation, Pagination, Autoplay } from "swiper/modules";

import SAImg from '../../assets/images/Projects/CMS/SA.jpg'
import PTImg from '../../assets/images/Projects/CMS/PT.jpg'
import CAITImg from '../../assets/images/Projects/CMS/CAIT.jpg'
import POCImg from '../../assets/images/Projects/CMS/POC.jpg'

const CmsSwiper = () => {
    return (
        <Swiper
            modules={[Navigation, Pagination, Autoplay]}
            spaceBetween={0}
            slidesPerView={3}
            navigation
            pagination={{ clickable: true }}
            autoplay={{ delay: 3000, disableOnInteraction: false }}
        >
            <SwiperSlide className="py-4 px-2">
                <div className="CustomSwiperBox">
                    <a href="https://swastikaacademy.com/" target="_blank">
                        <img className="w-full mb-4" src={SAImg} alt="" />
                    </a>
                    <h3>Swastika Academy</h3>
                    <p>Designed and developed a responsive website using:</p>
                    <span>HTML5</span> <span>CSS</span> <span>JS</span> <span>Wordpress</span>
                </div>
            </SwiperSlide>
            <SwiperSlide className="py-4 px-2">
                <div className="CustomSwiperBox">
                    <a href="https://examportal.co.in/" target="_blank">
                        <img className="w-full mb-4" src={PTImg} alt="" />
                    </a>
                    <h3>Pariksha Taiyari</h3>
                    <p>Designed and developed a responsive website using:</p>
                    <span>HTML5</span> <span>CSS</span> <span>JS</span> <span>Wordpress</span>
                </div>
            </SwiperSlide>
            <SwiperSlide className="py-4 px-2">
                <div className="CustomSwiperBox">
                    <a href="https://cait.gridlearn.com/" target="_blank">
                        <img className="w-full mb-4" src={CAITImg} alt="" />
                    </a>
                    <h3>CAIT</h3>
                    <p>Designed and developed a responsive website using:</p>
                    <span>HTML5</span> <span>CSS</span> <span>JS</span> <span>Wordpress</span>
                </div>
            </SwiperSlide>
            <SwiperSlide className="py-4 px-2">
                <div className="CustomSwiperBox">
                    <a href="https://demo2.gridlearn.com/" target="_blank">
                        <img className="w-full mb-4" src={POCImg} alt="" />
                    </a>
                    <h3>Pradhan Online Centre</h3>
                    <p>Designed and developed a responsive website using:</p>
                    <span>HTML5</span> <span>CSS</span> <span>JS</span> <span>Wordpress</span>
                </div>
            </SwiperSlide>
        </Swiper>
    );
};

export default CmsSwiper;
