import React, { useEffect } from "react";
import './App.css';
import Header from './components/Header/Header';
import Intro from './pages/Intro/Intro';
import About from './pages/About/About';
import Achievement from './pages/Achievement/Achievement';
import Skills from './pages/Skills/Skills';
import Projects from './pages/Projects/Projects';
import GetinTouch from './pages/GetinTouch/GetinTouch';
import Footer from './components/Footer/Footer';

import MaintenanceImg from './assets/images/Maintenance/maintenance.png';

function App() {

  useEffect(() => { 
    const disableRightClick = (event) => {
      event.preventDefault();
    };

    document.addEventListener("contextmenu", disableRightClick);

    return () => {
      document.removeEventListener("contextmenu", disableRightClick);
    };
  }, []);

  return (
    <>
      <section className="dt-hidden">
        <div className="ProcessPageSection">
          <img src={MaintenanceImg} alt="" className="animate-bounce scrollTop" />
          The mobile version is still in progress; you can view it in the desktop version for now
        </div>
      </section>
      <Header />
      <Intro />
      <About />
      <Achievement />
      <Skills />
      <Projects />
      <GetinTouch />
      <Footer />
    </>
  );
}

export default App;
