import React from "react";
import { styled } from "@mui/material/styles";
import MuiDrawer from "@mui/material/Drawer";

const drawerWidth = 240;

const Drawer = styled(MuiDrawer)(() => ({
    "& .MuiDrawer-paper": {
        width: drawerWidth,
    },
}));

export default function PersistentDrawer() {
    const [open, setOpen] = React.useState(false);

    const handleDrawerToggle = () => {
        setOpen(!open);
    };

    return (
        <sidebar className="dt-hidden">
            <div>
                <i className="fa fa-home fixed top-0 left-0 text-white" onClick={handleDrawerToggle}></i>
            </div>

            <Drawer variant="temporary" open={open} onClose={handleDrawerToggle}>
                <div style={{ display: "flex", justifyContent: "end", alignItems: "center", padding: "10px" }}>
                    <i className="fa fa-angle-left" onClick={handleDrawerToggle}></i>
                </div>
                <ul>
                    <li>Home</li>
                </ul>
            </Drawer>
        </sidebar>
    );
}
